.pasture {
  &__title {
    font-size: rem(30);
    font-weight: bold;
  }
  &__explain {
    font-size: rem(14);
    font-weight: medium;
    line-height: ( 35 / 20 );
  }
  &__image {
    width: 47%;
    height: auto;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__attention {
    font-size: rem(10);
    font-weight: medium;
    line-height: ( 24 / 14 );
  }
}